* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Roboto', sans-serif;
}

@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,300;0,400;0,500;0,700;0,900;1,300;1,400;1,500;1,700;1,900&display=swap');

section {
  margin: 1rem 0;
  max-width: 100%;
}

html, body {
  width: 100%;
}

body {
  display: flex;
  justify-content: center;
  font-size: 16px;
  //overflow-x: hidden;
  //background: linear-gradient(to top, powderblue, pink);


  h2 {
    text-align: center;
    margin: .8rem;
    align-self: start;
    width: fit-content;
  }

  .wrapper {
    width: 80%;
    overflow-x: hidden;
    section.about_me {
      display: grid;
      grid-template-columns: 288px auto;
      grid-gap: .5rem;
      padding: .5rem;
      .photo_block {

        img {
          object-fit: cover;
          width: 100%;
          height: 100%;

        }
      }
      .info_block {
        display: grid;
        grid-template-rows: max-content max-content;
        .info {
          display: grid;
          grid-template-columns: max-content auto;
          grid-template-rows: repeat(4, min-content);
          grid-gap: 0 .5rem;
          p{
            margin: .2rem 0;
          }
          p:nth-of-type(2n+1){
            font-weight: 600;
          }
          p:nth-of-type(2n){
            font-style: italic;
          }
        }
        .quote {
          width: fit-content;
          blockquote {
            font-style: italic;
            padding: .5rem;
            p {
              padding: .3rem;
            }
            cite {
              display: block;
              width: 100%;
              text-align: right;
              padding: .5rem 0;
            }
          }
        }

      }

    }

    section.info_about_my_city {
      p {
        padding: 1rem;
      }
      .slider {
        width: 100%;
        display: grid;
        justify-content: space-around;
        grid-template-columns: repeat(auto-fit, minmax(180px, 250px));
        grid-gap: 1rem 0;
        a {
          height: 100%;
        }
        img {
          height: 100%;
          width: 100%;

        }
      }
    }

    section.my_mood {
      display: flex;
      flex-direction: column;
      align-items: center;
      .mood {
        display: grid;
        justify-content: center;
        width: 100%;
        grid-gap: 0 1rem;

        //grid-template-columns: repeat(3, 1fr);
        grid-template-columns: repeat(auto-fit, minmax(200px, 250px));
        //grid-template-rows: 250px;
        img {
          height: 100%;
          height: 250px;
          //padding: .3rem;
        }
        p {
          text-align: center;
        }
      }
    }

    section.records {
      display: flex;
      flex-direction: column;
      align-items: center;
      table {
        width: 90%;
        width: fit-content;
      }
    }

    section.films_block {
      display: flex;
      flex-direction: column;
      align-items: center;
      .films {
        width: 100%;
        display: grid;
        justify-content: space-around;
        grid-template-columns: repeat(auto-fit, minmax(180px, 180px));
        grid-gap: .5rem;
        div {
          padding: 0.5rem;
        }
        img {
          height: 100%;
          max-height: 250px;
          width: 100%;
        }
        a {
          display: block;
          width: 100%;
          text-align: center;
          text-decoration: none;
          color: black;

        }
      }
    }

    section.model_3D {
      display: flex;
      flex-direction: column;
      align-items: center;
      iframe {
        width: 90%;
        height: 90vmin;
      }
    }

    section.theam_mode{
      position: absolute;
      top: 0;
      right: 0;
      width: 2rem;
      height: 2rem;
      z-index: 10;
      border: 1px solid black;
      border-radius: .5rem;
      padding: .25rem;
      margin: 1rem;
      cursor: pointer;
      div{
        background: url(https://image.flaticon.com/icons/svg/727/727866.svg) no-repeat;
        background-size: 100% 100%;
        width: 100%;
        height: 100%;
      }
    }
  }

}

@media(max-width: 700px) {
  .wrapper {
    width: 100% !important;
  }
  section.records {
    table {
      width: 100% !important;
    }
  }
  section.about_me {
    grid-template-columns: 100% !important;
    grid-template-rows: max-content max-content !important;
  }

}
